	module.exports = {"data":{"markdownRemark":{"html":"<p>One cool thing about <code>id</code> attributes is that, like classes, you can style them using CSS.</p>\n<p>Here’s an example of how you can take your element with the <code>id</code> attribute of cat-photo-element and give it the background color of green.</p>\n<p>In your style element: <code>#cat-photo-element { background-color: green; }</code></p>\n<p>Note that inside your style element, you always reference classes by putting a <code>.</code> in front of their names. You always reference ids by putting a <code>#</code> in front of their names.</p>","fields":{"slug":"/certificates/use-an-id-attribute-to-style-an-element/"},"frontmatter":{"title":"Use an Id Attribute to Style an Element"}}},"pathContext":{"slug":"/certificates/use-an-id-attribute-to-style-an-element/"}}

